.s-top-categories {
    margin-top: rem(55);
    padding: rem(14) 0;
    background: rgba(0, 0, 0, 0.4);

    @media (min-width: 768px) {
        padding: rem(18) 0;
    }

    @media (min-width: 1024px) {
        padding: rem(22) 0;
    }

    @media (min-width: 1600px) {
        padding: rem(28) 0;
    }

    @media (min-width: 1920px) {
        padding: rem(15) 0;
    }

    &__title {
        margin-bottom: rem(60);
    }

    &__item {
        overflow: hidden;
        cursor: pointer;
        position: relative;
        height: rem(103);
        border-radius: rem(5);

        @media (min-width: 768px) {
            height: rem(136);
        }

        @media (min-width: 1024px) {
            height: rem(161);
        }

        @media (min-width: 1600px) {
            height: rem(187);
        }

        &-img {
            overflow: hidden;
            width: 100%;
            height: 100%;

            img {
                position: absolute;
                width: 100%;
                height: 100%;
                border-radius: rem(5);
                object-fit: cover;
            }
        }

        &-title {
            z-index: 2;
            position: absolute;
            bottom: rem(30);
            left: rem(15);
            font-size: rem(19);
            font-weight: 700;
            line-height: 1.2;
            color: $grey_light_more_2;
        }
    }

    &__slide {
        width: rem(146);
        height: auto;
        transition: 0.5s;

        @media (min-width: 768px) {
            width: rem(193);
        }

        @media (min-width: 1024px) {
            width: rem(228);
        }

        @media (min-width: 1600px) {
            width: rem(265);
        }
        &:after {
            z-index: -10;
            position: absolute;
            top: 50%;
            left: 50%;
            width: calc(100% + 5%);
            height: calc(100% + 5%);
            border: 0.125rem solid #c6c7d000;
            border-radius: 0.625rem;
            transform: translate(-50%, -50%);
            transition: 0.5s;
            filter: blur(0.125rem);
            content: "";
        }

        &:hover {
            &:after {
                border: 0.125rem solid #c6c7d0;
                content: "";
            }
        }
    }

    .active-categories {
        &:after {
            border: 0.125rem solid #c6c7d0;
            content: "";
        }
    }

    &__slider {
        max-width: rem(1040);
        margin: 0 auto;
    }

    .swiper-container {
        overflow: visible;
    }
}
