.s-advertising {
    position: relative;
    // background: url(/img/back/back.jpg);
    background-repeat: no-repeat;
    background-size: cover;

    @media (max-width: 600px) {
        &:before {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: linear-gradient(23.83deg, rgba(0, 0, 0, 0.8) 42.56%, rgba(0, 0, 0, 0) 91.77%);
        }
    }

    .l-default {
        max-width: rem(1720);
    }

    &__title {
        font-family: "Roboto";
        
        font-size: rem(38);
        font-weight: 600;
        line-height: 98%;
        background: linear-gradient(46.06deg, #18191f 14.09%, #262b38 78.25%);
        letter-spacing: -0.03em;
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        background-clip: text;
        text-fill-color: transparent;
        padding: rem(317) 0 rem(109);
        @media (min-width: 768px) {
            font-size: rem(50);
            padding: rem(50) 0 rem(95);
        }
        @media (min-width: 1024px) {
            font-size: rem(70);
            padding: rem(83) 0 rem(100);
        }
        @media (min-width: 1600px) {
            font-size: rem(110);
            padding: rem(176) 0 rem(110);
        }
        @media (min-width: 1920px) {
            font-size: rem(120);
            padding: rem(176) 0 rem(90);
        }
    }

    &::after {
        content: "эталон";
        position: absolute;
        right: -15%;
        bottom: -12%;
        font-size: rem(120);
        font-weight: 500;
        line-height: 110%;
        color: transparent;
        letter-spacing: -0.03em;
        opacity: .2;
        -webkit-text-stroke: rem(3) #ffffff22;
        text-stroke: rem(3) #ffffff22;

        @media (min-width: 768px) {
            right: -12%;
            bottom: -42%;
            font-size: rem(200);
        }
        @media (min-width: 1024px) {
            right: -6%;
            bottom: -42%;
            font-size: rem(250);
        }
        @media (min-width: 1600px) {
            font-size: rem(400);
        }
        @media (min-width: 1920px) {
            font-size: rem(400);
        }
    }
}
