.s-substitution {
    padding-bottom: rem(50);

    @media (max-width: 600px) {
        padding-bottom: rem(65);
    }
    
    &__title {
        margin-bottom: rem(21);
        font-size: rem(24);
        font-weight: 400 !important;
        line-height: rem(29);
        color: rgba(235, 235, 235, 0.102);
        letter-spacing: 0.3em;

        @media (min-width: 768px) {
            margin-bottom: rem(44);
            font-size: rem(33);
            line-height: rem(39);
            letter-spacing: 0.8em;
        }

        @media (min-width: 1024px) {
            margin-bottom: rem(48);
            font-size: rem(41);
            line-height: rem(48);
        }

        @media (min-width: 1440px) {
            margin-bottom: rem(94);
            font-size: rem(69);
            line-height: rem(81);
            letter-spacing: 0.7em;
        }

        @media (min-width: 1920px) {
            font-size: rem(82);
            line-height: rem(96);
            letter-spacing: 0.78em;
        }
    }

    &__content {
        display: flex;
        align-items: center;
        justify-content: space-between;
        color: $grey_light_more_2;
        flex-wrap: wrap;

        @media (min-width: 768px) {
            flex-wrap: nowrap;
        }
    }

    &__item {
        &-text {
            width: 100%;
            margin-bottom: rem(45);
            font-size: rem(17);
            font-weight: 300;
            line-height: rem(16);
            letter-spacing: -0.01em;

            @media (min-width: 768px) {
                width: rem(260);
                margin-bottom: rem(0);
            }

            @media (min-width: 1024px) {
                width: rem(354);
                font-size: rem(25);
                line-height: rem(24);
            }

            @media (min-width: 1600px) {
                width: rem(600);
                font-size: rem(38);
                line-height: rem(36);
            }

            @media (min-width: 1920px) {
                width: rem(720);
                font-size: rem(40);
                line-height: rem(35);
            }
        }

        &-count {
            width: min-content;
        }

        &-minprom {
            width: rem(212);

            @media (min-width: 1024px) {
                width: rem(275);
            }

            @media (min-width: 1600px) {
                width: rem(375);
            }

            @media (min-width: 1920px) {
                width: rem(700);
            }
        }
    }

    &__count {
        &-container {
            display: flex;
            align-items: center;
            font-size: rem(50);
            font-weight: 500;
            line-height: 1.2;
            text-transform: uppercase;

            @media (min-width: 1024px) {
                font-size: rem(80);
            }

            @media (min-width: 1600px) {
                font-size: rem(120);
            }
        }

        &-number {
            margin-right: rem(15);
            font-size: rem(50);
            font-weight: 500;
            line-height: rem(59);

            @media (min-width: 1600px) {
                font-size: rem(120);
                line-height: rem(141);
            }
        }

        &-plus {
            color: $red;
        }

        &-text {
            font-size: rem(10);
            font-weight: 400;
            line-height: rem(12);
            letter-spacing: 0.25em;

            @media (min-width: 1024px) {
                font-size: rem(15);
            }

            @media (min-width: 1600px) {
                font-size: rem(21);
                line-height: rem(25);
            }
        }
    }

    &__minprom {
        &-logo {
            margin-bottom: rem(17);
            font-size: rem(20);

            @media (min-width: 1024px) {
                margin-bottom: rem(33);
                font-size: rem(30);
            }

            @media (min-width: 1600px) {
                margin-bottom: rem(45);
            }

            @media (min-width: 1920px) {
                margin-bottom: rem(50);
            }
        }

        &-text {
            font-size: rem(12);
            font-weight: 400;
            line-height: rem(14);
            text-transform: uppercase;
            color: $white;

            @media (min-width: 1024px) {
                font-size: rem(15);
                line-height: rem(18);
            }

            @media (min-width: 1600px) {
                font-size: rem(21);
                line-height: rem(25);
            }
        }
    }
}
