.s-goods {
    padding-top: rem(136);

    @media (max-width: 1199px) {
        padding-top: rem(110);
    }

    @media (max-width: 600px) {
        padding-top: rem(60);
    }

    .l-default {
        max-width: rem(1520);
    }

    .number__two {
        display: block;
    }

    &__title {
        margin-bottom: rem(15);
        font-size: rem(19);
    }

    &__text {
        margin-bottom: rem(25);

        @media (min-width: 768px) {
            margin-bottom: rem(80);
        }

        @media (min-width: 1024px) {
            margin-bottom: rem(60);
        }

        @media (min-width: 1600px) {
            margin-bottom: rem(130);
        }

        &--prefix {
            font-size: rem(14);
            font-weight: 400;
            line-height: 1.2;
            color: $grey_light_more_2;

            @media (min-width: 768px) {
                font-size: rem(15);

                &:first-child {
                    margin-bottom: rem(15);
                }
            }

            @media (min-width: 1024px) {
                font-size: rem(17);
            }

            @media (min-width: 1600px) {
                font-size: rem(19);
            }

            &:first-child {
                margin-bottom: rem(10);
            }
        }
    }

    &__items {
        display: flex;
        margin: 0 rem(-10);
        flex-wrap: wrap;

        @media (min-width: 768px) {
            margin: 0 rem(-15);
        }
        @media (min-width: 1024px) {
            margin: 0 rem(-20);
        }
        @media (min-width: 1600px) {
            margin: 0 rem(-30);
        }
        @media (min-width: 1920px) {
            margin: 0 rem(-50);
        }

        .last-item {
            position: relative;
            width: 100%;
            max-width: fit-content;
            margin: rem(35) auto 0;
            border-radius: rem(5);

            @media (min-width: 768px) {
                width: calc(33.3333% - 30px);
                max-width: none;
                margin: rem(15);

                .s-goods__item-info,
                .s-goods__item-buttons {
                    display: block;
                    opacity: 0.5;
                }
            }

            @media (min-width: 1024px) {
                width: calc(33.3333% - 20px);
                margin: rem(10);
            }

            @media (min-width: 1600px) {
                width: calc(33.3333% - 30px);
                margin: rem(15);
            }
            @media (min-width: 1920px) {
                width: calc(33.3333% - 50px);
                margin: rem(25);
            }

            .s-goods__item-info,
            .s-goods__item-buttons {
                display: none;
            }

            &::after {
                cursor: pointer;
                position: relative;
                top: 0;
                left: 0;
                display: flex;
                align-items: center;
                justify-content: center;
                width: 100%;
                height: 100%;
                margin: 0 auto;
                padding-top: 0;
                font-size: rem(13);
                font-weight: 600;
                line-height: 1.2;
                text-align: center;
                color: $white;
                border-radius: rem(5);
                content: "Следующая линейка";
                box-sizing: border-box;
                background-color: rgba(0, 0, 0, 0.722);

                @media (min-width: 768px) {
                    position: absolute;
                    padding-top: rem(60);
                }

                @media (min-width: 768px) {
                    font-size: rem(15);
                }

                @media (min-width: 1024px) {
                    font-size: rem(21);
                }

                @media (min-width: 1600px) {
                    font-size: rem(30);
                }

                @media (min-width: 1920px) {
                    font-size: rem(35);
                }
            }
        }
    }

    &__item {
        display: flex;
        justify-content: space-between;
        width: calc(50% - 20px);
        margin: rem(10);
        padding: rem(23);
        border: rem(1) solid transparent;
        border-radius: rem(5);
        border-image: linear-gradient(
            rgba(132, 132, 132, 1),
            rgba(195, 195, 195, 1),
            rgba(37, 37, 37, 1)
        );
        border-image-slice: 1;
        flex-direction: column;
        gap: rem(20);

        @media (min-width: 768px) {
            width: calc(33.3333% - 30px);
            margin: rem(15);
            padding: rem(28);
        }

        @media (min-width: 1024px) {
            width: calc(33.3333% - 20px);
            margin: rem(10);
            padding: rem(37);
        }

        @media (min-width: 1600px) {
            width: calc(33.3333% - 30px);
            margin: rem(15);
            padding: rem(60);
        }

        @media (min-width: 1920px) {
            width: calc(33.3333% - 50px);
            margin: rem(25);
        }

        &-img {
            max-width: rem(123);
            margin: 0 auto rem(12);
            box-sizing: border-box;

            @media (min-width: 768px) {
                max-width: rem(154);
                margin-bottom: rem(15);
            }

            @media (min-width: 1024px) {
                max-width: rem(200);
                margin-bottom: rem(16);
            }

            @media (min-width: 1600px) {
                max-width: rem(340);
                margin-bottom: rem(30);
            }

            img {
                width: 100%;
                margin: 0;
            }
        }

        &-text {
            min-height: rem(82);
            font-size: rem(10);
            @media (min-width: 1024px) {
                min-height: rem(74);
            }

            @media (min-width: 1024px) {
                min-height: rem(97);
                font-size: rem(13);
            }

            @media (min-width: 1600px) {
                min-height: rem(131);
                font-size: rem(19);
            }
        }

        &-name {
            margin-top: auto;
            margin-bottom: rem(5);
            font-weight: 400;
            line-height: 1.2;
            color: $grey_light_more_2;

            @media (min-width: 768px) {
                margin-bottom: rem(6);
            }

            @media (min-width: 1024px) {
                margin-bottom: rem(10);
            }

            @media (min-width: 1600px) {
                font-size: rem(21);
            }

            &:last-child {
                display: none;
            }
        }

        &-subtitle {
            margin-top: auto;
            margin-bottom: rem(5);
            font-weight: 400;
            line-height: 1.2;
            color: $grey_light;
            @media (min-width: 768px) {
                margin-bottom: rem(6);
            }

            @media (min-width: 1024px) {
                margin-bottom: rem(10);
            }
        }

        &-specifications {
            margin-bottom: rem(5);
            font-weight: 400;
            line-height: 1.2;
            color: $grey_light_more_2;

            @media (min-width: 768px) {
                margin-bottom: rem(20);
            }

            @media (min-width: 1024px) {
                margin-bottom: rem(30);
            }
        }

        &-more,
        &-buy {
            display: block;
            margin-top: auto;
            margin-bottom: rem(5);
            padding: rem(5) 0;
            font-size: rem(10);
            line-height: rem(12);
            text-align: center;
            color: $grey_light_more_2;
            border: rem(1) solid $grey_white;
            border-radius: rem(7);

            @media (min-width: 768px) {
                padding: rem(8) 0;
                font-size: rem(12);
                line-height: rem(14);
            }

            @media (min-width: 1024px) {
                padding: rem(12) 0;
            }
        }

        &-buy {
            margin-bottom: 0;
        }
    }
}
