.s-rulers {
    margin-top: rem(30);
    border-bottom: rem(1) solid $red;

    .l-default {
        max-width: rem(1520);
    }

    &__tabs {
        display: flex;
        margin: 0 0 rem(-1) 0;
        // border-bottom: rem(1) solid $red;
    }

    &__tab {
        margin: 0 0 rem(0) 0;
        font-size: rem(13);
        font-weight: 400;
        line-height: 1.2;

        color: $grey;
        border: 0.125rem solid rgba(0, 0, 0, 0);
        transition: 0.4s;
        box-sizing: border-box;
        border-bottom: 0.0625rem solid rgba(0, 0, 0, 0);
        @media (min-width: 768px) {
            font-size: rem(15);
        }
        @media (min-width: 1024px) {
            font-size: rem(19);
        }

        @media (min-width: 1600px) {
            font-size: rem(25);
        }

        @media (min-width: 1920px) {
            font-size: rem(21);
        }

        &-title {
            margin: rem(12) 0;
            padding: 0 rem(20);
            transition: 0.4s;
            border-right: rem(2) solid $grey;
            span {
                display: none;
                @media (min-width: 768px) {
                    display: inline-block;
                }
            }
        }
        &:last-child {
            border-right: none;
        }
        :hover {
            color: $grey_light_more_2;
        }

        &.is-active {
            margin-left: rem(-1);
            color: $grey_light_more_2;
            border: rem(2) solid $red;
            border-bottom: rem(1) solid $black;
            box-sizing: border-box;
            .s-rulers__tab-title {
                display: flex;
                padding: 0 rem(40);
                border: none;
                span {
                    display: inline-block;
                    margin-right: 5px;
                }
            }
        }
        &-content {
            display: none;
        }
    }
    .border-none {
        border: none;
        .s-rulers__tab-link {
            // border: none;
        }
        .s-rulers__tab-title {
            border: none;
        }
    }
}
.is-actived {
    display: block !important;
}
